import axios from 'axios'
import Qs from 'qs'

// console.log(config)
// axios.defaults.baseURL=config.homePage;
// axios.defaults.timeout = 10000;//设置请求超时
// axios.defaults.headers.post['Content-Type']='application/x-www-form-urlencoded;charset=UTF-8'; //测试没有生效
// axios.defaults.withCredentials = true;//请求带上cookie
// 请求格式化
axios.interceptors.request.use((config) => {
  let harr=['Content-Type','content-type'];
  // for(let v of harr){
  //   if (config.headers[v] == 'application/x-www-form-urlencoded;charset=UTF-8'){
  //     config.data = Qs.stringify(config.data);
  //     return config;
  //   }
  // }
  // console.log(config)
  if (config.method === 'post' && config.headers) {
    for(let v of harr){
      if (config.headers&&config.headers[v]&&~config.headers[v].indexOf('application/x-www-form-urlencoded')){
        config.data = Qs.stringify(config.data);
        return config;
      }
    }
  }
  return config;
}, (err) => {
  console.log(err);
  return Promise.reject(err);
});

//添加JSON格式判断
axios.isJson=(str)=>{
  try {
    JSON.parse(str);
  } catch (e) {
    console.log(e);
    return false;
  }
  return true;
}

//节流
axios.clickDelay=()=>{
  let timer=null;
  return function(s){
    return !timer&&(timer=setTimeout(()=>{
      console.log(timer);
      timer=null
    },s||2000))
  }
}
//防抖动
axios.preventShake=()=>{
  let timer=null;
  return function(fn){
    if(timer) clearTimeout(timer);
    timer=setTimeout(function(){
      typeof fn=="function"&&fn();
    },2000)
  }
}
export default {
  axios
}



