<template>
  <div>
    <!-- 选择部门页面 -->
    <div class="request-box" v-show="type == 1">
      <p>请选择您要办理的业务部门</p>
      <div style="margin-top: .9rem;" class="department-box">
        <div class="request-bottom"
             v-for="(item, index) in departmentList.list"
             :key="index"
             @click="getType(item)">
            <div class="space-border">
              <p>{{item.deptName}}</p>
              <p>当前排队<span>{{item.waitCount}}</span>人</p>
            </div>
        </div>
      </div>
      <div class="page">
        <div class="query-botton" 
             v-if="androidData.useIdCard"
             @click="queryProgress">查询排队进度</div>
        <div v-else></div>
        <div>
          <div class="page-botton" 
             v-if="departmentList.pageNum != 1"
             @click="prePage">上一页</div>
          <div class="page-botton" 
              v-if="departmentList.pageNum != departmentList.pages"
              @click="nextPage">下一页</div>
        </div>
      </div>
    </div>

    <!-- 选择业务类别页面 -->
    <div class="request-box" v-show="type == 2">
      <p>请选择您要办理的业务类别</p>
      <div class="back-box">
        <img src="../../../static/img/fanhui@2x.png" 
             @click="type = 1, backTime(90)">
        <img src="../../../static/img/shouye@2x.png"
             @click="backIndex">
      </div>
      <div class="department-box">
        <div class="request-bottom"
             v-for="(item, index) in businessCategory.list"
             :key="index"
             @click="getBusiness(item)">
          <p>{{item.describes}}</p> 
        </div>
      </div>
      <div class="page">
        <div class="back-index">
          <p><span style="font-size: .48rem !important;">{{time}}</span>S</p>
          <p>自动返回首页</p>
        </div>
        <div class="page-botton" 
             v-if="businessCategory.pageNum != 1"
             @click="prePage">上一页</div>
        <div class="page-botton"
             v-if="businessCategory.pageNum != businessCategory.pages"
             @click="nextPage">下一页</div>
      </div>
    </div>

    <!-- 选择业务页面 -->
    <div class="request-box" v-show="type == 3">
      <p>请选择您要办理的业务</p>
      <div class="back-box">
        <img src="../../../static/img/fanhui@2x.png" 
             @click="type = 2, backTime(90)">
        <img src="../../../static/img/shouye@2x.png"
             @click="backIndex">
      </div>
      <div class="department-box">
        <div class="request-bottom"
             v-for="(item, index) in businessData.list"
             :key="index"
             @click="getIdCard(item)">
          <p class="businessTitle"
             v-for="(data, index) in textSplit(item.describes)" 
             :key="'department_'+index">{{data}}</p> 
        </div>
      </div>
      <div class="page">
        <div class="back-index">
          <p><span style="font-size: .48rem !important;">{{time}}</span>S</p>
          <p>自动返回首页</p>
        </div>
        <div class="page-botton"
             v-if="businessData.pageNum != 1"
             @click="prePage">上一页</div>
        <div class="page-botton"
             v-if="businessData.pageNum != businessData.pages"
             @click="nextPage">下一页</div>
      </div>
    </div>

    <!-- 刷身份 -->
    <div class="request-box" v-show="type == 4">
      <p @click="androidGiveIdCard()">刷身份证确认{{queryData ? '查询' : '取号'}}</p>
      <div class="back-box">
        <img src="../../../static/img/fanhui@2x.png" 
             @click="JudgeReturn">
        <img src="../../../static/img/shouye@2x.png"
             @click="backIndex">
      </div>
      <p class="id-text">请将身份证放置下方识读区静置2-5秒</p>
      <div class="IdCard-box">
        <img src="../../../static/img/img@2x.png" alt="">
      </div>
      <div class="page">
        <div class="back-index" style="margin-right: 0;">
          <p><span style="font-size: .48rem !important;">{{time}}</span>S</p>
          <p>自动返回首页</p>
        </div>
      </div>
    </div>

    <!-- 取号页面 -->
    <div class="request-box" v-show="type == 5">
      <p>{{queryData ? '查询' : '取号'}}成功</p>
      <div class="back-box">
        <img src="../../../static/img/shouye@2x.png"
             @click="backIndex">
      </div>
      <p class="id-text">请在等候区等待叫号</p>
      <div class="number-box">
        <p>{{numberData.number}}</p>
        <p v-show="numberData.name">{{numberData.name}}</p>
        <p>{{numberData.serviceName}}</p>
        <p>当前等待<span>{{numberData.waitNumber}}</span>人</p>
      </div>
      <p class="number-text">请记住您的号码，窗口将在{{time}}s后自动返回首页</p>
    </div>

    <!-- 查询排队号 -->
    <div class="request-box" v-show="type == 6">
      <p></p>
      <div class="back-box">
        <img src="../../../static/img/shouye@2x.png"
             @click="backIndex">
      </div>
      <p class="id-text">排队进度查询</p>
      <div class="query-box">
        <p>抱歉！</p>
        <p>暂未查询到您的排队信息。</p>
      </div>
      <div class="page">
        <div class="back-index" style="margin-right: 0;">
          <p><span style="font-size: .48rem !important;">{{time}}</span>S</p>
          <p>自动返回首页</p>
        </div>
      </div>
    </div>

    <!-- <div id="test-box">{{test_err}}</div> -->
    <!-- <loading-box></loading-box> -->
  </div>
</template>

<script>
import loadingBox from '@/components/loading/index.vue'

export default {
  props: ['androidData', 'departmentList'],
  components: {
    loadingBox,
  },
  data() {
    return {
      queryData: false,
      time: 90,
      timeOut: null,
      deparmentState: null,
      firstDeptId: null,
      secondDeptId: null,
      secondParentId: null,
      //重复点击限制
      disabled: false, //上一页、下一页、部门、业务列表
      finishDisabled:false, //取号
      
      type: 1,
      pageNumber: 1,
      pageNumber2: 1,
      pageNumber3: 1,
      businessCategory: {},
      businessData: {},
      business: {},
      numberData: {},

      //测试
      // test_err:"-"
    }
  },
  watch: {
    type: {
      /**
       * 监听是否首页
       */
      handler(val,old) {
        if(val == 1) {
          this.queryData = false
          clearInterval(this.timeOut);
        }
        /**
         * 关闭身份证读卡
         */
        if (old == 4) {
          if(window.ycsb && window.ycsb.actionCloseRead) {
            window.ycsb.actionCloseRead()
          }
        }
      },
    }
  },
  mounted () {
    /**
     * 身份证信息传递挂载
     */
    window['androidGiveIdCard'] = (val) => {
      this.androidGiveIdCard(val)
    }
  },
  methods: {
    /**
     * 文字分割
     * @param {String} data 业务名称
     */
    textSplit(data) {
      return data.split('#')
    },
    /**
     * socket推号修改等待人数
     * @param {Object} val 部门参数
     */
    changeWaitingNumber(val) {
      this.departmentList.list.forEach((item) => {
        if(item.id == val.deptId) {
          item.waitCount = val.waitNumber
        }
      })
    },
    /**
     * 上一页
     */
    prePage() {
      if (this.type == 1) {
        // this.$emit("pageClick") //点击测试
        if(this.disabled) {
          return
        }
        this.disabled = true
        if(this.pageNumber>1){
          this.pageNumber -= 1
        }
        this.$emit('prePage', this.pageNumber)
      } else if (this.type == 2) {
        if(this.pageNumber2>1){
          this.pageNumber2 -= 1
        }
        this.getType()
      } else if (this.type == 3) {
        if(this.pageNumber3>1){
          this.pageNumber3 -= 1
        }
        this.getBusiness()
      }
    },
    /**
     * 下一页
     */
    nextPage() {
      if (this.type == 1) {
        // this.$emit("pageClick") //点击测试
        if(this.disabled) {
          return
        }
        this.disabled = true
        if(this.pageNumber<this.departmentList.pages){
          this.pageNumber += 1
        }
        this.$emit('nextPage', this.pageNumber)
      } else if (this.type == 2) {
        if(this.pageNumber2<this.businessCategory.pages){
          this.pageNumber2 += 1
        }
        this.getType()
      } else if (this.type == 3) {
        if(this.pageNumber3<this.businessData.pages){
          this.pageNumber3 += 1
        }
        this.getBusiness()
      }
    },
    /**
     * 返回首页
     */
    backIndex() {
      this.$emit('backIndex')
      this.type = 1
      this.pageNumber = 1
    },
    /**
     * 获取部门信息
     * @param {Object} params 传参信息
     * @param {Function} callback 回调
     */
    getBusinessClasses(params, callback) {
      this.axios.post('/remoteservice/api/getBusinessClasses.do', params).then((res) => {
        callback(res)
      })
    },
    /**
    /**
     * 判断返回
     */
    JudgeReturn() {
      if(this.queryData) {
        this.type = 1
      } else {
        this.type = 3
        this.backTime(90)
      }
    },
    /**
     * 查询排队进度
     */
    queryProgress() {
      this.queryData = true
      this.type = 4
      if(window.ycsb) {
        window.ycsb.actionLoadInfo()
      }
      this.backTime(90)
    },
    /**
     * 获取部门里类别
     * @param {Object} val 部门信息
     */
    getType(val) {
      // this.$emit("pageClick") //点击测试
      // 防重复提交
      if(this.disabled) {
        return
      }
      this.disabled = true
      if(this.type == 1) {
        this.pageNumber2 = 1
      }
      if (val) {
        this.firstDeptId = val.id
      }
      // this.$emit("pageRequest") //点击测试
      this.getBusinessClasses({
        pageNum: this.pageNumber2,
        pageSize: 9,
        deptId: this.firstDeptId,
        parentId: 0,
        serviceKey: this.androidData.serviceKey,
      }, (res)=>{
        if(res.data.result) {
          this.businessCategory = res.data.resultList
          this.type = 2
          this.backTime(90)
        } else {
          this.$toast(res.data.msg)
        }
        this.$nextTick(()=>{
          this.disabled = false
        })
      })
    },
    /**
     * 获取业务类别
     * @param {Object} val 信息
     */
    getBusiness(val) {
      // 防重复提交
      if(this.disabled) {
        return
      }
      this.disabled = true
      if(this.type == 2) {
        this.pageNumber3 = 1
      }
      if (val) {
        this.secondDeptId = val.deptId
        this.secondParentId = val.id
      }
      this.getBusinessClasses({
        pageNum: this.pageNumber3,
        pageSize: 9,
        deptId: this.secondDeptI,
        parentId: this.secondParentId,
        serviceKey: this.androidData.serviceKey,
      }, (res)=>{
        if (res.data.result) {
          this.businessData = res.data.resultList
          this.type = 3
          this.backTime(90)
        } else {
          this.$toast(res.data.msg)
        }
        this.$nextTick(()=>{
          this.disabled = false
        })
      })
    },
    /**
     * 叫号
     * @param {Function} callback 回调函数
     */
    takeBusinessNumber(params,callback) {
      // this.$emit("countClick"); //点击测试
      if(this.finishDisabled) {
        return
      }
      this.finishDisabled = true
      // this.$emit("countRequest"); //点击测试
      this.axios.post('/remoteservice/api/getBusinessNumber.do', params).then((res) => {
        callback(res)
        this.finishDisabled = false
      }).catch((e)=>{
        this.finishDisabled = false
      })
    },
    /**
     * 获取身份证信息
     * @param {Object} val 业务信息
     */
    getIdCard(val) {
      this.business = val
      if(this.androidData.useIdCard) {
        /**
         * 使用身份证取号
         */
        this.type = 4
        this.backTime(90)
        if(window.ycsb) {
          window.ycsb.actionLoadInfo()
        }
      } else {
        /**
         * 不用身份证取号
         */
        this.takeBusinessNumber({
          serviceKey: this.androidData.serviceKey,
          typeId: this.business.id,
          numberName: '',
          idCardNumber: '',
        }, (res) => {
          if (res.data.result) {
            this.numberData = res.data
            this.type = 5
            this.backTime(15)
            this.$$nextTick(()=>{
              this.finishDisabled = false
            })
          } else {
            this.$toast(res.data.msg)
          }
        })
      }
    },
    /**
     * 安卓传送身份证信息
     * @param {Object} val 身份证信息
     */
    androidGiveIdCard(val) {
      if (this.queryData) {
        /**
         * 查询
         */
        this.axios.post('/remoteservice/api/searchBusinessInfo.do', {
          idCardNumber: val.identityNum,
          serviceKey: this.androidData.serviceKey,
        }).then((res) => {
          if (res.data.result) {
            this.numberData = res.data
            this.type = 5
            this.backTime(15)
          } else if (!res.data.result && res.data.msg == '当前用户没有取号信息') {
            this.type = 6
            this.backTime(15)
          } else {
            this.$toast(res.data.msg)
          }
        })
      } else {
        /**
         * 取号
         */
        this.takeBusinessNumber({
          serviceKey: this.androidData.serviceKey,
          typeId: this.business.id,
          numberName: val.identityName,
          idCardNumber: val.identityNum,
        }, (res) => {
          if (res.data.result) {
            this.numberData = res.data
            this.type = 5
            this.backTime(15)
          } else {
            this.type = 1
            this.$toast(res.data.msg)
          }
          this.queryData = false
        })
      }
    },
    /**
     * 倒计时返回首页
     * @param {Number} val 倒计时时间
     */
    backTime(val) {
      clearInterval(this.timeOut);
      this.time = val
      this.timeOut = setInterval(() => {
        this.time -= 1
        if (this.time <= 0) {
          this.type = 1
          clearInterval(this.timeOut);
        }
      }, 1000);
    },
  },
}
</script>

<style scoped>
.request-box {
  height: 100%;
}
.request-box >p:nth-child(1) {
  color: #2F3870;
  font-size: .42rem;
  font-weight: 600;
}
.department-box {
  display: flex;
  flex-wrap: wrap;
  /* justify-content: space-around; */
  /* margin-top: .9rem; */
  
  padding: 0 .3rem;
  min-height: 7.4rem; 
}
.back-box {
  display: flex;
  justify-content: flex-start;
  padding-left: .5rem;
}
.back-box img {
  width: 1.14rem;
  height: 1.14rem;
}
.request-bottom {
  width: 3.21rem;
  height: 2.42rem;
  background: url('../../../static/img/button@2x.png');
  background-size: 100% 100%;
  margin: 0 .06rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  word-wrap: break-word;
  word-break: break-all;
}
.space-border{
  box-sizing:border-box;
  margin:.25rem .25rem .3rem;
  overflow:hidden;
}
.request-bottom p {
  padding: 0 .3rem;
}
.request-bottom p:nth-child(1) {
  color: #fff;
  font-size: .35rem;
  line-height: 1.2em;
}
.request-bottom p:nth-child(2) {
  color: #fff;
  font-size: .26rem;
  margin-top: .1rem;
}
.businessTitle {
  font-size: .42rem !important;
  margin: 0 !important;
}
.request-bottom span {
  font-size: .35rem;
  margin: 0 .1rem;
  color: #FFFC00;
}
.page {
  margin-top: .6rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: .5rem;
  padding-left: .5rem;
}
.page >div:nth-child(2) {
  display: flex;
}
.back-index {
  margin-right: 2.6rem;
}
.back-index :nth-child(1){
  font-size: .24rem; 
  text-align: left;
  color: #E57700;
}
.back-index :nth-child(2){
  font-size: .16rem; 
  color: #2F3870;
}
.query-botton {
  width: 3.12rem;
  height: 1.01rem;
  background: url('../../../static/img/lineUp.png');
  background-size: 100% 100%;
  line-height: .95rem;
  color: #fff;
  font-size: .3rem;
  margin-right: 1.9rem;
}
.page-botton {
  width: 2.4rem;
  height: 1.01rem;
  background: url('../../../static/img/button3@2x.png');
  background-size: 100% 100%;
  line-height: .9rem;
  color: #fff;
  font-size: .3rem;
  display: flex;
  justify-content: center;
}
.id-text {
  color: #2F3870;
  margin-top: .2rem;
  font-size: .4rem;
  font-weight: 600;
}
.IdCard-box {
  width: 9.4rem;
  height: 6.21rem;
  border-radius: .2rem;
  background:rgba(89,109,171,1);
  box-shadow:0px 3px 21px 0px rgba(43,55,141,0.5);
  margin: 0.4rem auto 0;
}
.IdCard-box img {
  width: 5.77rem;
  height: 5.14rem;
  margin-top: .6rem;
}
.number-box,.query-box {
  width: 9.4rem;
  height: 6.21rem;
  border-radius: .2rem;
  background:rgba(255,255,255,1);
  box-shadow:0px 3px 21px 0px rgba(43,55,141,0.5);
  margin: 0.4rem auto 0;
  display: inline-block;
}
.number-text {
  margin-top: .9rem;
  color: #2F3870;
  font-size: .4rem;
}
.number-box p:nth-child(1) {
  color: #2F3870;
  font-size: 1.8rem;
  margin-top: .6rem;
}
.number-box p:nth-child(2) {
  color: #E57700;
  font-size: .48rem;
  /* margin-top: .2rem; */
}
.number-box p:nth-child(3) {
  color: #2F3870;
  font-size: .6rem;
  font-weight: 500;
  margin-top: .2rem;
}
.number-box p:nth-child(4) {
  color: #2F3870;
  font-size: .26rem;
  margin-top: .2rem;
}
.number-box span {
  color: red;
  font-size: .46rem;
  margin: 0 .2rem;
}
.query-box p {
  color: #2F3870;
  font-size: .6rem;
}
.query-box p:nth-child(1) {
  margin-top: 2rem;
}
#test-box{
  position:fixed;
  bottom:1rem;
  right:.5rem;
  background:rgba(0,0,0,.3);
  color:red;
}
</style>



