<template>
  <div class="remind-box">
    <p class="remind-text" v-show="callNumber">请 {{callNumber}} 到一体机前办理业务</p>
    <div class="mask-box"></div>
    <div style="margin-top: .3rem;">
      <el-carousel trigger="click" 
                   arrow="never"
                   height="4.8rem">
        <el-carousel-item v-for="(item, index) in remind_list" :key="index">
          <div class="business-type">
            <p>{{item.deptName}}</p>
            <div class="call-text">
              <p style="margin-left: .4rem;">呼叫</p>
              <div v-if="item.processServiceName">
                <p>{{item.proceNumber || '-'}}</p>
                <p v-if="item.callName">{{replaceName(item.callName) || '-'}}</p>
                <p style="min-width: 3rem;">{{item.processServiceName || '-'}}</p>
              </div>
              <div v-else>
                <p>空闲</p>
              </div>
            </div>
            <div>
              <p style="margin-left: .4rem;">等待</p>
              <div v-if="item.nextServiceName">
                <p>{{item.nextNumber || '-'}}</p>
                <p v-if="item.nextName">{{replaceName(item.nextName) || '-'}}</p>
                <p style="min-width: 3rem;">{{item.nextServiceName || '-'}}</p>
              </div>
              <div v-else>
                <p>空闲</p>
              </div>
            </div>
          </div>
          <div class="swiper-slide_bottom2"></div>
          <div class="swiper-slide_bottom1"></div>
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>

<script>

export default {
  props: ['remindList', 'callNumber'],
  computed:{
    remind_list(){
      return this.remindList
    }
  },
  data() {
    return {
    }
  },
  mounted(){
  },
  methods: {
    /**
     * 名字大码
     @param {String} name 人名
     */
    replaceName(name) {
      if(name && name.length > 2) {
        return `${name.substring(0, 1)}*${name.substring(2, name.length)}`
      } else if (name && name.length == 2) {
        return `${name.substring(0, 1)}*`
      }
    },
  },
}
</script>

<style scoped>
>>>.el-carousel__button {
  background-color: #666 !important;
  width: .14rem !important;
  height: .14rem !important;
  border-radius: 50% !important;
  margin: 0 .02rem;
}
>>>.el-carousel__indicators .is-active .el-carousel__button{
  background-color: #007aff !important;
}

.remind-box {
  height: 6rem;
  position: relative;
}
.remind-text {
  color: #E57700;
  font-size: .36rem;
  margin: .3rem 0;
  font-weight: 500;
}
.swiper-container {
  /* width: 9.6rem; */
  height: 4.8rem;
  margin: .3rem auto .2rem;
  background: #EBEDFF;
}
.swiper-slide_bottom2 {
  width: 10.2rem;
  height: 3.2rem;
  background: url('../../../static/img/bg2.png');
  background-size: 100% 122%;
  border-radius:16px;
  position: absolute;
  margin-top:  .46rem;
  margin-left: .3rem;
}
.swiper-slide_bottom1 {
  width: 9.9rem;
  height: 3.7rem;
  background: url('../../../static/img/bg2.png');
  background-size: 100% 122%;
  border-radius:16px;
  position: absolute !important;
  margin-top:  .2rem;
  margin-left: .45rem;
}
.business-type {
  width: 9.6rem;
  margin: 0 auto;
  height: 4.8rem;
  /* background: linear-gradient(108deg,rgba(39,52,128,1),rgba(36,38,91,1));
  box-shadow: 0px 3px 35px 0px rgba(34,103,255,0.18); */
  border-radius: .2rem;
  position: absolute;
  z-index: 99999;
  margin-left: .61rem;
  background: url('../../../static/img/bg2.png');
  background-size: 100% 102%;
}
.business-type >p{
  display: inline-block;
  font-size: .42rem;
  color: #fff;
  margin: .3rem 0 .15rem;
}

.business-type >div {
  margin: .15rem auto;
  width: 9.20rem;
  height: 1.16rem;
  background:rgba(234,245,255,1);
  border:1px solid rgba(194, 198, 232, 1);
  box-shadow:0px 3px 7px 0px rgba(0, 0, 0, 0.39) inset;
  border-radius: .1rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: #154BC3;
}
.business-type >div >p {
  font-size: .42rem;
  font-weight: 500;
}
.business-type >div >div {
  display: flex;
  align-items: center;
  justify-content: space-around;
  min-width: 7rem;
}
.business-type >div >div >p{
  font-size: .42rem;
  font-weight: 500;
}
.call-text >p{
  color: #E57700 !important;
}
.call-text >div >p{
  color: #E57700 !important;
}
.mask-box {
  height: 5rem;
  width: 100%;
  position: absolute;
  bottom:0;
  z-index: 99999;
}
</style>



