<template>
  <div class="web-index" v-show="showWeb">
    <div class="web-head">
      <div class="head-text__left">
        <p>视联网远程申办自助申请终端</p>
        <p>{{remindList[0] && remindList[0].serviceName ? remindList[0].serviceName : androidData.serviceName}}</p>
        <h4 id="test-btn" v-show="click_test"><span>点击：</span>{{cc}}<br><span>请求：</span>{{rqc}}</h4>
      </div>
      <div class="data-content">
        <div>
          <p>{{date.days}}</p>
          <p>{{date.weekday}}</p>
        </div>
        <p>{{date.time}}</p>
      </div>
    </div>
    <remind-box
      :callNumber="callNumber"
      :remindList="remindList"></remind-box>
    <request-box
      ref="request"
      :departmentList="departmentList"
      @nextPage="nextPage"
      @prePage="prePage"
      @backIndex="backIndex"
      :androidData="androidData"
      @countClick="countClick"
      @countRequest="countRequest"
      @pageClick="pageClick"
      @pageRequest="pageRequest"></request-box>
  </div>
</template>

<script>
import remindBox from '@/components/remind/index.vue'
import requestBox from '@/components/requestNumber/index.vue'

export default {
  components: {
    remindBox,
    requestBox,
  },
  data () {
    return {
      showWeb: false,
      socketUrl: "ws://" + window.location.host +"/remoteservice/h5SocketServer",
      date: {},
      androidData: {},
      departmentList: [],
      remindList: [],
      disabled: false,
      socket:null,
      _timer:null,
      callNumber: null,
      heart: null,
      heartNumber: 0,
      socket_timeout:15000, //socket连接超时时间
      //测试参数
      test_mark:false, //PC测试标记
      click_test:false, //点击测试
      rqc:0, //请求计数
      cc:0, //点击次数
    }
  },
  watch: {
    'androidData.serviceKey': {
      handler() {
        this.newSocket()
      },
    }
  },
  created () {
    this.getTime()
  },
  mounted () {
    /**
     * 给与安卓H5版本号
     */
    if(window.ycsb && window.ycsb.actionSaveHTMLVersion) {
      window.ycsb.actionSaveHTMLVersion('1.1')
    }
    //PC测试标记
    if(this.test_mark){
      this.newSocket()
    }
    /**
     * 获取实时本地时间
     */
    setInterval(() => {
      this.getTime()
    }, 10000);
    /**
     * 获取安卓设置参数
     */
    window['getAndroidSettingData'] = (val) => {
      this.getAndroidSettingData(val)
    }
    /**
     * 一键检测
     */
    window['loadCarderState'] = (val) => {
      this.loadCarderState(val)
    }
  },
  methods: {
    /**
     * 32寸叫号机检测
     */
    loadCarderState(data) {
      this.sendMessage({
        type: 13,
        serviceKey: this.androidData.serviceKey,
        cardReader: data,
        deviceCode: this.androidData.deviceCode,
        // printMode: '',
      })
    },
    /**
     * 心跳链接 (每15秒发送一次，四次没有返回断开主动断开socket，并重置心跳次数)
     */
    heartLink() {
      this.heart = setInterval(() => {
        this.__heartMessage({
          serviceKey: this.androidData.serviceKey,
          deviceCode: this.androidData.deviceCode,
          pageSize: 9,
          pageNum: 1,
          state: 0,
          type: 10,
        })
        this.heartNumber++;
        if(this.heartNumber>4){
          this.socket.close()
          this.heartNumber=0;
        }
      }, 15000);
    },
    /**
     * 返回首页
     */
    backIndex() {
      this.sendMessage({
        serviceKey: this.androidData.serviceKey,
        deviceCode: this.androidData.deviceCode,
        pageSize: 9,
        pageNum: 1,
        state: 0,
        type: 16,
      })
    },
    /**
     * 上一页
     */
    prePage(val) {
      // 防重复提交
      if(this.disabled) {
        return 
      }
      this.disabled = true
      this.sendMessage({
        serviceKey: this.androidData.serviceKey,
        deviceCode: this.androidData.deviceCode,
        pageSize: 9,
        pageNum: val,
        state: 0,
        type: 16,
      })
    },
    /**
     * 部门下一页
     */
    nextPage(val) {
      // 防重复提交
      if(this.disabled) {
        return 
      }
      this.disabled = true
      this.sendMessage({
        serviceKey: this.androidData.serviceKey,
        deviceCode: this.androidData.deviceCode,
        pageSize: 9,
        pageNum: val,
        state: 0,
        type: 16,
      })
    },
    /**
     * soekct创建
     */
    newSocket() {
      if(typeof(WebSocket) === 'undefined') {
        this.$toast('您的浏览器不支持socket')
      } else {
        if(this.test_mark){
          this.androidData.serviceKey = "RS_4c575969018a4e1abc00738243fc7873";
          this.socket = new WebSocket("ws://" + '172.16.140.150:8080' +"/remoteservice/h5SocketServer")
        }else{
          this.socket = new WebSocket(this.socketUrl)
        }

        // 监听socket连接
        this.socket.onopen = this.socketOpen
        // 监听socket错误信息
        this.socket.onerror = this.sockeError
        // 监听socket消息
        this.socket.onmessage = this.getSocketMessage
        // 监听socket关闭事件
        this.socket.onclose = this.socketClose
      }
    },
    // 参数格式化
    _formatParams(param){
      let str = null;
      if(!param){param=this.param}
      switch(Object.prototype.toString.call(param)){
        case '[object Object]': {
          str = JSON.stringify(param)
        }break;
        case '[object String]': {str = param}break;
        case '[object Number]':{
          str = JSON.stringify({type: param + ""})
        }break;
        default:str=null;
      }
      return str
    },
    //实际发送方法
    sendMessage(param,callback){
      let agentData = this._formatParams(param)
      if(!this.socket){return}
      if (this.socket.readyState === this.socket.OPEN) {
        // this.socketReconnecting = false;
        // console.log("sendMessage",1,this.socket.OPEN)
        //若是ws开启状态
        this.socket.send(agentData||null);
      }else if (this.socket.readyState === this.socket.CONNECTING) {//正在连接
        // 若是 正在开启状态，则等待1s后重新调用
        // console.log("sendMessage",2,this.socket.CONNECTING)
        // if(this._timer){clearTimeout(this._timer);this._timer=null}
        this._timer = setTimeout(()=>{
          this.sendMessage(agentData,callback);
        }, 1000);
      }else if(this.socket.readyState === this.socket.CLOSING){
        if(this._timer){clearTimeout(this._timer);this._timer=null}
      }else {
        // console.log("sendMessage",3)
        // 若未开启 ，则等待1s后重新调用
        // if(this._timer){clearTimeout(this._timer);this._timer=null}
        this._timer = setTimeout(()=>{
          this.sendMessage(agentData,callback);
        }, 1000);
      }
    },
    __heartMessage(agentData){//用于心跳，不设置发送失败延时
      agentData = agentData?JSON.stringify(agentData):null;
      if (this.socket&&this.socket.readyState === this.socket.OPEN) {
        this.socket.send(agentData||null);
      }
    },
    /**
     * socket链接成功
     */
    socketOpen() {
      console.log('网络连接成功')
      /**
       * 发送消息
       * RS_a4c9af959ae041baa8c686463889cd6b 测试数据
       */
      this.sendMessage({
        serviceKey: this.androidData.serviceKey,
        deviceCode: this.androidData.deviceCode,
        type: 15,
      })
      this.sendMessage({
        serviceKey: this.androidData.serviceKey,
        deviceCode: this.androidData.deviceCode,
        pageSize: 9,
        pageNum: 1,
        state: 0,
        type: 16,
      })
      /**
       * socket心跳
       */
      this.heartLink()
      this.socket_reopen_count = 0; //重置socket重启次数
      if(window.ycsb && window.ycsb.actionCloseLoading) {
        window.ycsb.actionCloseLoading()
      }
    },
    /**
     * 获取socket数据
     * @param {Object} msg soekct返还数据
     */
    getSocketMessage(msg) {
      let res = JSON.parse(msg.data);
      console.log(res)
      if(window.ycsb && window.ycsb.actionSaveSoketMsg) {
        window.ycsb.actionSaveSoketMsg(JSON.stringify(res))
      }
      /**
       * 判断服务中心不存在调用安卓方法
       */
      if(res.msg == '服务中心不存在') {
        if(window.ycsb && window.ycsb.actionIntentSettingAct) {
          window.ycsb.actionIntentSettingAct('服务中心不存在')
        }
        return;
      }
      /**
       * 判断叫号机不存在调用安卓方法
       */
      if(res.msg == '设备不存在') {
        if(window.ycsb && window.ycsb.actionIntentSettingAct) {
          window.ycsb.actionIntentSettingAct('服务器异常')
        }
        return;
      }
      /**
       * type==16  查询部门数据
       */
      if(res.type == 10){
          this.heartNumber = 0
      }else if (res.type == 16) {
        this.departmentList = res.resultList||[]
        if(window.ycsb && window.ycsb.actionCloseLoading) {
          window.ycsb.actionCloseLoading()
        }
      } else if (res.type == 17) {
       /**
       * type==17  部门等待人数推送
       */
        this.$refs.request.changeWaitingNumber(res)
      } else if (res.type == 6) {
        /**
         * 一键检测32寸叫号机
         */
        if(window.ycsb && window.ycsb.actionLoadCarderState) {
          window.ycsb.actionLoadCarderState()
        }
      }else if(res.type == 18){
        if(window.ycsb && window.ycsb.actionIntentSettingAct) {
          window.ycsb.actionIntentSettingAct('设备不存在')
        }
      } else {
        if(res.length) {
          let toastNumber = false
          res.forEach((val) => {
            if(val.proceNumber && val.proceNumber != this.callNumber) {
              if(val.numServiceKey && this.androidData.serviceKey == val.numServiceKey) {
                this.setSpeech(val.proceNumber)
                this.callNumber = val.proceNumber
              }
            }
            if(val.proceNumber && this.androidData.serviceKey == val.numServiceKey) {
              toastNumber = true
            }
          })
          if (!toastNumber) {
            this.callNumber = ''
          }
        }
        this.remindList = [...res];
        /**
         * 叫号提醒socket推送
         */
        if (Array.isArray(res) && res.length && res[0].serviceName != this.androidData.serviceName) {
          if(window.ycsb && window.ycsb.actionChangeServiceName) {
            window.ycsb.actionChangeServiceName()
          }
        }
      }
      /**
       * 关闭安卓加载动画
       */
      if(window.ycsb && window.ycsb.actionCloseLoading) {
        window.ycsb.actionCloseLoading()
      }
      this.showWeb = true
      /**
       * 防重复提交
       */
      this.disabled = false
      this.$refs.request.disabled = false
    },
    /**
     * socket关闭 
     */
    socketClose(){
      // if(window.ycsb && window.ycsb.actionShowLoading) {
      //   window.ycsb.actionShowLoading()
      // }
      setTimeout(()=>{
        this.newSocket()
      },1000)
    },
    /**
     * socket报错
     */
    sockeError() {
      // this.$toast('网络连接异常')
      // if(window.ycsb && window.ycsb.actionIntentSettingAct) {
      //   window.ycsb.actionIntentSettingAct('网络连接异常')
      // }
    },
    /**
     * 语音播报
     * @param {String} val 叫号机号码
     */
    setSpeech(val) {
      if(window.ycsb && window.ycsb.actionPlayProcessNum) {
        window.ycsb.actionPlayProcessNum(val.toString())
      }
    },
    /**
     * 获取安卓系统配置信息
     * @param {Object} val 安卓系统设置参数
     */
    getAndroidSettingData(val) {
      this.androidData = val
    },
    /**
     * 时间转换
     */
    getTime() {
      let time = new Date()
      let y = time.getFullYear()
      let m = time.getMonth() + 1
      let d = time.getDate()
      let h = time.getHours()
      let f = time.getMinutes()
      let w = time.getDay()
      let weekdays = ["星期日","星期一","星期二","星期三","星期四","星期五","星期六"]
      return this.date = {
        days: `${y}/${m}/${d}`,
        time: f < 10 ? `${h}:0${f}`:`${h}:${f}`,
        weekday: weekdays[w],
      }
    },
    /**
     * 点击测试
     */
    countClick(){
      this.cc++;
    },
    countRequest(){
      this.rqc++;
    },
    pageClick(){
      this.cc++;
    },
    pageRequest(){
      this.rqc++;
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.web-index {
  background-color: #EBEDFF;
  height: 100%;
  overflow-y: auto;
  /* display: inline-block; */
}
.web-head {
  background: url('../../../static/img/bg@2x.png');
  background-size: 100% 100%;
  height: 1.63rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  padding: 0 .3rem .15rem;
}
.head-text__left p:nth-child(1) {
  font-size: .48rem; 
  font-weight:500;
}
.head-text__left p:nth-child(2) {
  font-size: .18rem; 
  text-align: left;
  /* margin-top: .12rem; */
}
.data-content {
  display: flex;
  align-items: center;
}
.data-content >p {
  font-size: .58rem;
  font-weight:500;
}
.data-content >div >p {
  font-size: .12rem !important;
  text-align: right;
  margin-right: .11rem;
}
/* .data-content >div >:nth-child(2) {
  margin-top: .08rem;
} */
#test-btn{
  width:.8rem;
  padding:.1rem;
  background-color:rgba(50,50,50,.5);
  border-radius:50%;
  position:fixed;
  top:.1rem;
  right:.5rem;
  color:yellowgreen;
}
#test-btn span{
  font-size:.1rem;
  color:#fff;
}
</style>



