<template>
  <div class="tosst-box"
       v-show="visible">
       <div class="tosst-content">{{message}}</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      visible: false,
      message: '',
    }
  },
}
</script>

<style scoped>
.tosst-content {
  position: absolute;
  min-height: 1rem;
  line-height: 1rem;
  min-width: 4rem;
  text-align: center;
  border-radius: .1rem;
  color: #fff;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  background: rgba(0,0,0,.9);
  padding: .1rem .4rem;
}
</style>



